import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import './Login.css';

function Login() {
    const history = useHistory();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isLoggedIn, setLoggedIn] = useState(false);

  function handleEmailChange(event) {
    setEmail(event.target.value);
  }

  function handlePasswordChange(event) {
    setPassword(event.target.value);
  }

  function handleSubmit() {
    const isPccsEmail = email.endsWith('@pccsk12.com');
    if (isPccsEmail) {
        setLoggedIn(true);
    }
    else {
        alert('Invalid username or password - PCCS email required for security');
    }
  }

  function handleSignup() {
    alert('Please email routerangers@pccsk12.com for your login info');
  }

  function handleForgotPassword() {
    alert('Please email routerangers@pccsk12.com for your lost password, for safety reasons we do not allow users to manually reset their password');
  }

  if (isLoggedIn) {
    //return <Redirect to="/dashboard" />;
    history.push('/dashboard');
  }

  return (
    <div className="login-page">

        <section className="vh-150 gradient-custom">
        <div className="container py-5 h-100">
            <div className="row d-flex justify-content-center align-items-center h-100">
            <div className="col-12 col-md-8 col-lg-6 col-xl-5">
                <div className="card bg-dark text-white">
                <div className="card-body p-5 text-center">

                    <div className="mb-md-5 mt-md-4 pb-5">

                    <h2 className="fw-bold mb-2 text-uppercase">Login</h2>
                    <p className="text-white-50 mb-5">Please enter your login and password!</p>

                    <form onSubmit={handleSubmit}>
                        <div className="form-outline form-white mb-4">
                            <input type="email" id="typeEmailX" className="form-control form-control-lg"value={email} onChange={handleEmailChange} />
                            <label className="form-label" htmlFor="typeEmailX">Email
                            </label>
                        </div>

                        <div className="form-outline form-white mb-4">
                            <input type="password" id="typePasswordX" className="form-control form-control-lg" value={password} onChange={handlePasswordChange}/>
                            <label className="form-label" htmlFor="typePasswordX">Password
                            </label>
                        </div>

                        <p className="small mb-5 pb-lg-2"><a className="text-white-50" href="#!" onClick={handleForgotPassword}>Forgot password?</a></p>

                        <button className="btn btn-outline-light btn-lg px-5" type="submit" onClick={handleSubmit}>Login</button>

                        <div className="form-check mb-0">
                            <input className="form-check-input me-2" type="checkbox" value="" id="form2Example3" />
                            <label className="form-check-label" htmlFor="form2Example3">
                                Remember me
                            </label>
                        </div>
                    </form>
                    </div>

                    <div>
                        <p className="mb-0">Don&#39;t have an account? <a href="#!" className="text-white-50 fw-bold" onClick={handleSignup}>Sign Up</a>
                        </p>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
    </div>
  );
}

export default Login;
